
.calendar-wrap{
	width:90%;
	margin:0.2rem auto;
}
.calendar-wrap .icon-img{
	width:.5rem;
}
.calendar-wrap .ym{
	font-size:.5rem;
	color:rgb(45,45,45);
	font-weight: 600;
	padding-left: .1rem;
}
.calendar-wrap table{
	width:100%;
	text-align: center;
	font-size: .5rem;
	color:rgb(131,131,131);
}
.calendar-wrap td{
	border:.02rem solid rgb(236,236,236);
	border-radius: .1rem;
	font-size:.4rem;
	padding:.2rem;
}
.calendar-wrap .checked{
	background:rgb(244,96,148);
	border-color:rgb(244,96,148);
	color:#fff;
}
.calendar-wrap .emptys{
	border:0;
}	



